import React from 'react'
import {Flex, WhiteSpace} from 'antd-mobile';
import {SearchNav, GoodsList, ScrollVertical, VsonBanner} from 'components'
import {Dialog} from 'utils'
import {mallActions} from 'action'
import {entries} from "reducer";
import {connect} from "react-redux";
import {bindActionCreators} from 'redux'
import {Toast} from 'antd-mobile'
import './index.scss'

export interface MallHomeProps {
    toggleGoodsTypes: any,
    showGoodsTypes: boolean,
    getBanners: any,
    getHomeGoods: any,
    bannerList: any[],
    goodsList: any[],
    goodsTypes: any[],
    history: any
}

class MallHome extends React.Component<MallHomeProps> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    choseGoodsType = (type: number) => {
        if (type !== 4) {
            this.props.history.push('/mall/goods_list/' + type)
        } else {
            Toast.info('装修中')
        }
    }

    toggleGoodsTypes = (scrollNode: any) => {
        const {toggleGoodsTypes, showGoodsTypes} = this.props
        const {scrollTop} = scrollNode
        if ((scrollTop > 180 && !showGoodsTypes) || (scrollTop < 180 && showGoodsTypes)) {
            toggleGoodsTypes()
        }
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {props: {bannerList, goodsTypes, goodsList, showGoodsTypes}, pageInit, choseGoodsType, toggleGoodsTypes} = this
        return (
            <Flex direction={'column'} className={'mall_home'}>
                <Flex className={`top`}>
                    <img src="/lsc_h5_hash/imgs/shopHomeLogo.png" alt="" />
                    <Flex.Item>
                        <SearchNav />
                    </Flex.Item>
                    <img src="/lsc_h5_hash/imgs/msg_0.png" alt="" onClick={Dialog.showDownloadModal} />
                </Flex>
                <Flex.Item className={'main_content'}>
                    {
                        showGoodsTypes &&
                        <Flex className={'goods_types_fixed'}>
                            {
                                goodsTypes.map((e, i) => (
                                    <Flex.Item key={i} onClick={() => choseGoodsType(e.value)}>{e.text}</Flex.Item>
                                ))
                            }
                        </Flex>
                    }
                    <ScrollVertical
                        onScroll={toggleGoodsTypes}
                        onLoadData={pageInit}
                    >
                        <VsonBanner options={bannerList} />
                        <WhiteSpace size={'sm'} />
                        <Flex justify={'around'} className={'goods_types'}>
                            {
                                goodsTypes.map((e, i) => (
                                    <Flex.Item key={i} className={'goods_type'} onClick={() => choseGoodsType(e.value)}>
                                        <img alt={e.text} src={e.src} />
                                        <div>{e.text}</div>
                                    </Flex.Item>))
                            }
                            <Flex.Item key={5} className={'goods_type'} onClick={() => choseGoodsType(4)}>
                                <img alt={''} src='/lsc_h5_hash/imgs/shoucang@2x.png' />
                                <div>收藏</div>
                            </Flex.Item>
                        </Flex>
                        <WhiteSpace size={'sm'} />
                        <GoodsList list={goodsList} />
                    </ScrollVertical>
                </Flex.Item>
            </Flex>
        );
    }

    pageInit = async () => {
        const {getBanners, getHomeGoods} = this.props
        await getBanners()
        await getHomeGoods()
    }

    componentDidMount(): void {
        this.pageInit()
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

function mapStateToProps(state: entries) {
    const {bannerList, goodsList, showGoodsTypes} = state.mallReducer
    const {goodsTypes} = state.commonReducer
    return {
        showGoodsTypes,
        bannerList,
        goodsList,
        goodsTypes
    }
}

function mapDispatchToProps(dispatch: any) {
    const {getBanners, getHomeGoods, toggleGoodsTypes} = mallActions
    return {
        ...bindActionCreators({getBanners, getHomeGoods, toggleGoodsTypes}, dispatch),
        dispatch
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(MallHome)
