import React from 'react'
import {AllowRight} from 'components'
import {Flex} from 'antd-mobile'
import {entries} from "reducer";
import {commonActions} from 'action'
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {Dialog} from 'utils'

import './index.scss'

interface P {
    orderTypes: any,
    getUserStore: any,
    storeInfo: any
}

interface S {
}


class Mine extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {orderTypes, storeInfo: {storeType='2', storeImg = {}, ...others}},
            state: {}
        } = this
        return (
            <div className={'mine_page'} onClick={Dialog.showDownloadModal}>
                <AllowRight className={'store'}>
                    <img className={'logo'} src={storeType === '2' ? "/lsc_h5_hash/imgs/logo.png" : storeImg.medium}
                         alt="" />
                    <Flex.Item>
                        <div className={'title'}>{storeType === '2' ? '您还未开通店铺' : others.storeName}</div>
                        <div className={'tips'}>{storeType === '2' ? '去开通' : '进入我的店铺'}</div>
                    </Flex.Item>
                </AllowRight>
                <div className={'label'}>卖家中心</div>
                <div className={'item  order_center'}>
                    <AllowRight>
                        <Flex.Item>我的订单</Flex.Item>
                        <div className={'all_order'}>
                            查看全部订单
                        </div>
                    </AllowRight>
                    <Flex className={'order_types'}>
                        {
                            orderTypes.map((e: any, i: number) =>
                                <div key={i}>
                                    <img src={e.src} alt="" />
                                    <div>{e.text}</div>
                                </div>
                            )
                        }
                    </Flex>
                </div>
                <div className={'my_actions item'}>
                    <AllowRight className={'action'}>
                        <img className={'icon'} src="/lsc_h5_hash/imgs/publish_1.png" alt="" />
                        <Flex.Item>
                            我发布的商品
                        </Flex.Item>
                    </AllowRight>
                    <AllowRight className={'action bd_ud'}>
                        <img className={'icon'} src="/lsc_h5_hash/imgs/shop.png" alt="" />
                        <Flex.Item>
                            店铺余额
                        </Flex.Item>
                    </AllowRight>
                    <AllowRight className={'action'}>
                        <img className={'icon'} src="/lsc_h5_hash/imgs/publish_0.png" alt="" />
                        <Flex.Item>
                            发布新品
                        </Flex.Item>
                    </AllowRight>
                </div>
                <div className={'label'}>买家中心</div>
                <div className={'item  order_center'}>
                    <AllowRight>
                        <Flex.Item>我的订单</Flex.Item>
                        <div className={'all_order'}>
                            查看全部订单
                        </div>
                    </AllowRight>
                    <Flex className={'order_types'}>
                        {
                            orderTypes.map((e: any, i: number) =>
                                <div key={i}>
                                    <img src={e.src} alt="" />
                                    <div>{e.text}</div>
                                </div>
                            )
                        }
                    </Flex>
                </div>
                <div className={'my_actions item'}>
                    <AllowRight className={'action'}>
                        <img className={'icon'} src="/lsc_h5_hash/imgs/auction.png" alt="" />
                        <Flex.Item>
                            参加的竞拍
                        </Flex.Item>
                    </AllowRight>
                    <AllowRight className={'action bd_ud'}>
                        <img className={'icon'} src="/lsc_h5_hash/imgs/buy_car.png" alt="" />
                        <Flex.Item>
                            我的购物车
                        </Flex.Item>
                    </AllowRight>
                    <AllowRight className={'action'}>
                        <img className={'icon'} src="/lsc_h5_hash/imgs/collection.png" alt="" />
                        <Flex.Item>
                            我的收藏
                        </Flex.Item>
                    </AllowRight>
                </div>
                <div className={'label'}>个人中心</div>
                <div className={'my_actions item'}>
                    <AllowRight className={'action'}>
                        <img className={'icon'} src="/lsc_h5_hash/imgs/notice.png" alt="" />
                        <Flex.Item>
                            消息中心
                        </Flex.Item>
                    </AllowRight>
                    <AllowRight className={'action bd_ud'}>
                        <img className={'icon'} src="/lsc_h5_hash/imgs/setting.png" alt="" />
                        <Flex.Item>
                            设置
                        </Flex.Item>
                    </AllowRight>
                </div>
            </div>
        );
    }

    componentDidMount(): void {
        this.getInitData()
    }

    getInitData = async () => {
        const {getUserStore} = this.props
        await getUserStore()
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

function mapStateToProps(state: entries) {
    const {orderTypes, storeInfo} = state.commonReducer
    return {
        orderTypes,
        storeInfo
    }
}

function mapDispatchToProps(dispatch: any) {
    const {getUserStore} = commonActions
    return {
        ...bindActionCreators({getUserStore}, dispatch),
        dispatch
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Mine)
