import React from 'react'
import {entries} from "reducer";
import {commonActions, treasureForumActions, treasureForumTypes} from "action";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {Flex} from 'antd-mobile'
import {VsonTabs, ScrollVertical, TreasureItem, EmptyData} from 'components'
import {Dialog} from 'utils'

import './index.scss'

const tabsProps = {
    label: 'typeName',
    value: 'typeId'
}

const refreshOpts = [
    {
        type: treasureForumTypes.changeTreasureData,
        payload: {
            goodsList: []
        }
    }
]

interface P {
    getTreasureDetail: any,
    changeTreasureData: any,
    changeCommonData: any,
    getTreasureTypes: any,
    getTreasure: any,
    forumTypes: any[],
    treasureTypes: any[],
    treasureForumList: any[],
    formFields: any,
    userInfo: any,
    page: number
}

interface S {
}

class TreasureForum extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {treasureTypes, forumTypes, treasureForumList, userInfo: {headImg = {}}},
            state: {},
            getList,
            onTabsChange,
            onOperate
        } = this
        return (
            <Flex direction={'column'} className={'treasure_forum_page'}>
                <Flex className={'top bd_d'}>
                    <img className={'header'} src={headImg.medium || "/lsc_h5_hash/imgs/tab5_normal@2x.png"} alt=""
                         onClick={Dialog.showDownloadModal} />
                    <VsonTabs className={'forum_types'} options={forumTypes} name={'forumType'}
                              onChange={onTabsChange} />
                    <img className={'margin_lr'} src="/lsc_h5_hash/imgs/add1.png" alt="" onClick={Dialog.showDownloadModal} />
                    <img src="/lsc_h5_hash/imgs/msg_0.png" alt="" onClick={Dialog.showDownloadModal} />
                </Flex>
                <VsonTabs name={'sayType'} options={treasureTypes} tabsProps={tabsProps} onChange={onTabsChange} />
                <Flex.Item>
                    <ScrollVertical onLoadData={getList} refreshOpts={refreshOpts}>
                        {
                            treasureForumList.map((e: any, i: number) => <TreasureItem
                                treasure={e} key={i}
                                onOperate={() => onOperate(e.sayId, i)} />)
                        }
                        {
                            !treasureForumList.length && <EmptyData />
                        }
                    </ScrollVertical>
                </Flex.Item>
            </Flex>
        );
    }

    componentDidMount(): void {
        this.getInitData()
    }

    getInitData = async () => {
        const {getTreasureTypes, changeTreasureData} = this.props
        const {list} = await getTreasureTypes()
        const arr = [{
            typeName: '全部',
            typeId: ''
        }, ...list]
        await changeTreasureData({treasureTypes: arr})
        this.getList()
    }

    onOperate = async (sayId: string, index: number) => {
        const {getTreasureDetail, changeTreasureData, treasureForumList} = this.props
        const treasure = await getTreasureDetail(sayId)
        treasureForumList[index] = {sayId, ...treasure}
        await changeTreasureData({treasureForumList: [...treasureForumList]})
    }

    onTabsChange = async () => {
        const {changeCommonData, changeTreasureData} = this.props
        await changeTreasureData({treasureForumList: []})
        await changeCommonData({page: 1, totalPage: 0})
        this.getList()
    }

    getList = async () => {
        const {getTreasure, forumTypes, formFields: {forumType, sayType}, page} = this.props
        await getTreasure({
            url: forumTypes[forumType].query,
            data: {
                page,
                sayType: sayType ? JSON.stringify([sayType]) : ''
            }
        })
    }


    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}


function mapStateToProps(state: entries) {
    const {treasureForumList, treasureTypes} = state.treasureForumReducer
    const {forumTypes, formFields, page, userInfo} = state.commonReducer
    return {
        treasureTypes,
        forumTypes,
        formFields,
        treasureForumList,
        page,
        userInfo
    }
}

function mapDispatchToProps(dispatch: any) {
    const {changeCommonData} = commonActions
    const {getTreasure, changeTreasureData, getTreasureTypes, getTreasureDetail} = treasureForumActions
    return {
        ...bindActionCreators({
            getTreasureTypes,
            changeCommonData,
            getTreasure,
            changeTreasureData,
            getTreasureDetail
        }, dispatch),
        dispatch
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(TreasureForum)
