import React from 'react'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import {Flex} from 'antd-mobile'
import {VsonTabs, ScrollVertical, SearchNav, VsonPopover, MuseumList, AsyncPca} from 'components'
import {WxConfig} from 'utils'

import './index.scss'
import {entries} from "reducer";
import {museumActions, commonActions} from "action";

interface P {
    className?: string,
    museumNatures: any[],
    museumSorts: any[],
    museumScope: any[],
    museumList: any[],
    myMuseums: any[],
    museumMyNavs: any[],
    formFields: any,
    getMuseums: any,
    getMyMuseums: any,
    changeCommonData: any,
    page: number,
    totalPage: number,
    history: any
}

interface S {
}

class Museum extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                className = '',
                museumNatures,
                museumSorts,
                museumScope,
                formFields,
                museumMyNavs,
                museumList
            },
            state: {},
            getInit,
            getList,
            onSortChange,
            navMine
        } = this
        museumSorts[1].title = <AsyncPca onChange={getList}>区域</AsyncPca>
        return (
            <Flex className={`museum_home`} direction={"column"}>
                <Flex className={'top_nature bd_d'}>
                    <VsonPopover options={museumScope} name={'territory'} onChange={getInit}>
                        <div className={'left_nature'}>
                            <span>{formFields.territory === '1' ? '国际' : '国内'}</span>
                            <img src="/lsc_h5_hash/imgs/down.png" alt="" />
                        </div>
                    </VsonPopover>
                    <VsonTabs name={'nature'} onChange={getInit} className={'natures'} activeColor={'#6499AE'}
                              options={museumNatures} />
                    <VsonPopover options={museumMyNavs} name={'mine'} onChange={navMine}>
                        <div className={'right_nature'}>
                            <span>{'我的'}</span>
                            <img src="/lsc_h5_hash/imgs/down.png" alt="" />
                        </div>
                    </VsonPopover>
                </Flex>
                <Flex className={'top_sort bd_d'}>
                    <VsonTabs onChange={onSortChange} name={'sort'} className={'sorts'} activeColor={'#6499AE'}
                              options={museumSorts} />
                    <img src="/lsc_h5_hash/imgs/msg_0.png" alt="" />
                </Flex>
                <div className={'museum_search'}>
                    <SearchNav className={'museum_nav'} placeholder='请输入博物馆名称' page={'/museums/museum_list/search'} />
                </div>
                <Flex.Item className={formFields.sort === 2 ? 'distance_list' : 'grid_list'}>
                    <ScrollVertical onLoadData={getList}>
                        <MuseumList showStyle={formFields.sort === 2 ? 'distance' : 'grid'} list={museumList} />
                    </ScrollVertical>
                </Flex.Item>
            </Flex>
        );
    }

    navMine = async (val: any, e: any) => {
        const {history, myMuseums} = this.props
        if (val === '0') {
            const museum = myMuseums[0]
            history.push({pathname: !!museum ? '/museums/museum_home/' + museum.museumId : '/museums/museum_edit'})
        } else {
            history.push({pathname: e.page})
        }
    }

    getList = async () => {
        const {getMuseums, formFields: {nature, pca = [], territory = 0, sort = 0, longitude, latitude}, page} = this.props
        await getMuseums({
            nature, territory, sort, page, area: [...pca].pop(), longitude, latitude
        }, 'list')
    }

    getInit = async () => {
        const {changeCommonData, getMyMuseums} = this.props
        await changeCommonData({page: 1, totalPage: 0})
        await this.getList()
        await getMyMuseums()
    }

    onSortChange = async ({value}: any) => {
        const {changeCommonData, formFields} = this.props
        if (value === 1) return
        if (value === 2) {
            // @ts-ignore
            const {longitude, latitude} = await WxConfig.getLocation()
            await changeCommonData({formFields: {...formFields, longitude, latitude}})
        }
        this.getInit()
    }

    componentDidMount(): void {
        this.getInit()
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

function mapStateToProps(state: entries) {
    const {museumNatures, museumSorts, museumScope, formFields, page, totalPage, museumMyNavs, myMuseums} = state.commonReducer
    const {museumList} = state.museumReducer
    return {
        museumNatures, museumSorts, museumScope, formFields, page, totalPage,
        museumList, museumMyNavs, myMuseums
    }
}

function mapDispatchToProps(dispatch: any) {
    const {getMuseums} = museumActions
    const {changeCommonData, getMyMuseums} = commonActions
    return {
        ...bindActionCreators({getMuseums, changeCommonData, getMyMuseums}, dispatch),
        dispatch
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Museum)
