import React from 'react'
import {TabBar} from 'antd-mobile'
import {connect} from "react-redux";
import {Route} from "react-router-dom"
import {commonActions} from 'action'
import {entries} from 'reducer'
import {pages} from 'pages/tabbars'

import './index.scss'
import {bindActionCreators} from "redux";

interface homeProps {
    tabBarList: any[],
    selectTabBar: any,
    history: any,
    match: any
}

class Home extends React.Component<homeProps> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    choseTab = (page: string, index: number) => {
        const {selectTabBar, history} = this.props
        selectTabBar(index)
        history.push('/home/' + page)
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {props: {tabBarList}, choseTab} = this
        return (
            <TabBar
                tintColor={'#27B1CE'}
            >
                {
                    tabBarList.map((e: any, index) =>
                        <TabBar.Item
                            title={e.text}
                            key={e.page}
                            selected={e.isSelected}
                            icon={<img className='tab_image'
                                       src={'/lsc_h5_hash/imgs/tab' + (index + 1) + '_normal@2x.png'}
                                       alt={e.text} />}
                            selectedIcon={<img className='tab_image'
                                               src={'/lsc_h5_hash/imgs/tab' + (index + 1) + '_selected@2x.png'}
                                               alt={e.text} />}
                            onPress={() => choseTab(e.page, index)}
                        >
                            <Route path={'/home/' + e.page} component={pages[e.page]} />
                        </TabBar.Item>)
                }
            </TabBar>
        );
    }

    componentDidMount(): void {
        const {tabBarList, match: {params: {sub}}, selectTabBar} = this.props
        const index = tabBarList.findIndex(e => e.page === sub)
        const {isSelected} = tabBarList[index]
        if (!isSelected) {
            selectTabBar(index)
        }
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }

    componentWillReceiveProps(nextProps: Readonly<homeProps>, nextContext: any): void {

    }
}

const mapStateToProps = (state: entries) => ({
    ...state.commonReducer,
    ...state.mallReducer
});

function mapDispatchToProps(dispatch: any) {
    const {selectTabBar} = commonActions
    return {
        ...bindActionCreators({selectTabBar}, dispatch),
        dispatch
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Home)
