import React from 'react'
import {entries} from "reducer";
import {Dialog} from "utils";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {Flex} from 'antd-mobile'

import './index.scss'

interface P {
    galleryItems: any
}

class Gallery extends React.Component<P> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {galleryItems}
        } = this
        return (
            <Flex className={'gallery_page'} wrap={'wrap'}>
                {
                    galleryItems.map((e: any, i: number) => <div
                        key={i}
                        onClick={Dialog.showDownloadModal}
                        className={'tool'}
                        style={{backgroundImage: "url(/lsc_h5_hash/imgs/360backView_" + i + ".png)"}}>
                        <div>{e.text}</div>
                        <img src={"/lsc_h5_hash/imgs/lepai360_" + i + '.png'} alt="" />
                    </div>)
                }
            </Flex>
        );
    }

    componentDidMount(): void {
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

function mapStateToProps(state: entries) {
    const {galleryItems} = state.commonReducer
    return {
        galleryItems
    }
}

function mapDispatchToProps(dispatch: any) {
    return {
        ...bindActionCreators({}, dispatch),
        dispatch
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Gallery)
